import { Base64DataBuffer } from "@mysten/sui.js";
import { filter, lastValueFrom, map, take } from "rxjs";
import { createMessage } from "_messages";
import { WindowMessageStream } from "_messaging/WindowMessageStream";
import { isErrorPayload } from "_payloads";
import { ALL_PERMISSION_TYPES } from "_payloads/permissions";
import { deserializeSignaturePubkeyPair } from "_src/shared/signature-serialization";
function mapToPromise(stream, project) {
    return lastValueFrom(stream.pipe(take(1), map((response) => {
        if (isErrorPayload(response)) {
            // TODO: throw proper error
            throw new Error(response.message);
        }
        return project(response);
    })));
}
export class DAppInterface {
    constructor() {
        this._messagesStream = new WindowMessageStream("ethos_in-page", "ethos_content-script");
    }
    openWallet() {
        return mapToPromise(this.send({
            type: "open-wallet",
        }), (response) => response.success);
    }
    hasPermissions(permissions = ALL_PERMISSION_TYPES) {
        return mapToPromise(this.send({
            type: "has-permissions-request",
            permissions,
        }), (response) => response.result);
    }
    requestPermissions(permissions = ALL_PERMISSION_TYPES) {
        return mapToPromise(this.send({
            type: "acquire-permissions-request",
            permissions,
        }), (response) => response.result);
    }
    getAccounts() {
        return mapToPromise(this.send({
            type: "get-account",
        }), (response) => response.accounts);
    }
    requestPreapproval(preapproval) {
        return mapToPromise(this.send({
            type: "preapproval-request",
            preapproval,
        }), (response) => response);
    }
    executeMoveCall(transaction) {
        return mapToPromise(this.send({
            type: "execute-transaction-request",
            transaction,
        }), (response) => response.result);
    }
    executeSerializedMoveCall(transactionBytes) {
        return mapToPromise(this.send({
            type: "execute-transaction-request",
            transactionBytes,
        }), (response) => response.result);
    }
    signMessage(message) {
        let messageData;
        let messageString;
        // convert utf8 string to Uint8Array
        if (typeof message === "string") {
            messageString = message;
            message = new Uint8Array(Buffer.from(message, "utf8"));
        }
        // convert Uint8Array to base64 string
        if (message instanceof Uint8Array) {
            messageData = new Base64DataBuffer(message).toString();
        }
        return mapToPromise(this.send({
            type: "execute-sign-message-request",
            messageData,
            messageString,
        }), (response) => response.signature
            ? deserializeSignaturePubkeyPair(response.signature)
            : undefined);
    }
    disconnect() {
        return mapToPromise(this.send({
            type: "disconnect-request",
        }), (response) => response.success);
    }
    send(payload, responseForID) {
        const msg = createMessage(payload, responseForID);
        this._messagesStream.send(msg);
        return this._messagesStream.messages.pipe(filter(({ id }) => id === msg.id), map((msg) => msg.payload));
    }
}
